/*------------------------------------*\
    #MOBILE-PANE
\*------------------------------------*/

.c-mobile-pane {
	position: fixed;
	top: 0;
	left: 0;

	height: 100%;
	width: 100%;

	background-color: $color--darkBlue;

	visibility: hidden;

	transform: translateX(100%);
	transform-origin: left;

	z-index: 5000;
}

.c-mobile-pane__navigation {
	// display: flex;
	height: 100%;
	width: 100%;
	padding-top: $ui-header-height-mobile + $space-xs;

	@include bp("medium") {
		padding-top: $ui-header-height + $space-xs;
	}
}

.c-mobile-pane__twitter-wrapper {
	padding-top: 50px;
}

.c-mobile-pane__twitter {
	cursor: pointer;
	path {
		transition: $transition;
	}

	&:hover {
		path {
			fill: $color--gold;
		}
	}
}
